<template>
  <v-card class="mx-auto" max-width="500">
    <v-card-title class="title font-weight-regular justify-space-between">
      <span>{{ currentTitle }}</span>
      <v-avatar
        color="primary lighten-2"
        class="subheading white--text"
        size="24"
        v-text="step"
      ></v-avatar>
    </v-card-title>

    <v-window v-model="step">
      <v-window-item :value="1">
        <v-card-text>
          <v-text-field label="" v-model="account.username"></v-text-field>
          <v-text-field label="" v-model="account.email"></v-text-field>
          <span class="caption grey--text text--darken-1">
             ID 
          </span>
        </v-card-text>
      </v-window-item>

      <v-window-item :value="2">
        <v-card-text>
           : {{ question }}
          <v-text-field label="" v-model="account.answer"></v-text-field>
          <span class="caption grey--text text--darken-1">
                
          </span>
        </v-card-text>
      </v-window-item>

      <v-window-item :value="3">
        <v-card-text>
          <v-text-field type="password" label=" " v-model="account.password">
          </v-text-field>
          <v-text-field type="password" label=" " v-model="account.passwordCheck">
          </v-text-field>
          <span class="caption grey--text text--darken-1">
              
          </span>
        </v-card-text>
      </v-window-item>
    </v-window>

    <v-divider></v-divider>

    <v-card-actions>
      <!-- <v-btn :disabled="step === 1" text @click="step--">
        
      </v-btn> -->
      <v-spacer></v-spacer>
      <v-btn v-if="step <= 2" color="primary" depressed @click="findPassReq()">
        
      </v-btn>
      <v-btn v-if="step === 3" color="primary" depressed @click="updatePasswordReq()">
        
      </v-btn>
    </v-card-actions>
  </v-card>
</template>

<script>
import axios from "axios";

export default {
  name: "FindPass",
  data: () => ({
    step: 1,
    account: {
      username: "",
      email: "",
      passwordCheck: "",
      password: "",
      answer: ""
    },
    question: ""
  }),
  methods: {
    updatePasswordReq() {
      if (this.account.password === "" || this.account.passwordCheck === "") {
        alert("  ");
      } else if (this.account.password !== this.account.passwordCheck) {
        alert("   .");
      } else {
        axios
          .post(`${this.$serverURL}/newuser/change_pw`, null, {
            params: {
              accessToken: this.$store.state.accessToken,
              password: this.account.password
            }
          })
          .then(({ data }) => {
            if (data) {
              alert("!  .");
              this.$router.push({ name: "Home" });
              //  
            } else {
              alert(" ...");
            }
          })
          .catch(err => {
            console.error(err);
          });
      }
    },
    findPassReq() {
      if (this.step === 1) {
        if (this.account.username === "" || this.account.email === "") {
          alert("  ");
        } else {
          axios
            .post(`${this.$serverURL}/newuser/findpw`, null, {
              params: {
                email: this.account.email,
                username: this.account.username
              }
            })
            .then(({ data }) => {
              if (data.result) {
                this.step = 2;
                this.question = data.question;
              } else {
                alert("  ");
              }
            })
            .catch(err => console.log(err));
        }
      } else if (this.step === 2) {
        if (this.account.answer === "") {
          alert(" ");
        } else {
          axios
            .post(`${this.$serverURL}/newuser/quiz`, null, {
              params: {
                answer: this.account.answer,
                username: this.account.username
              }
            })
            .then(({ data }) => {
              if (data.result === "true") {
                this.$store.commit("LOGIN", data);
                this.step = 3;
                //   
              } else {
                alert("    .");
              }
            })
            .catch(err => console.log(err));
        }
      }
    }
  },

  computed: {
    currentTitle() {
      switch (this.step) {
        default:
          return " ";
      }
    }
  }
};
</script>

<style></style>
