<template>
  <v-card class="mx-auto" max-width="50vw">
    <v-card-title class="title font-weight-regular justify-space-between">
      <span></span>
    </v-card-title>

    <v-card width="50vw">
      <v-card-text>
        <v-form ref="form" v-model="valid" lazy-validation>
          <v-text-field
            @keyup="checkId"
            type="text"
            v-model="userData.username"
            :rules="idRules"
            label=""
            required
          ></v-text-field>
          <v-text-field
            v-model="userData.password"
            label=""
            :rules="textRules"
            type="password"
            required
          ></v-text-field>
          <v-text-field
            v-model="passwdcheck"
            :rules="pwRules"
            label="()"
            type="password"
            required
          ></v-text-field>
          <v-text-field
            @keyup="checkEmail"
            type="email"
            :rules="emailRules"
            v-model="userData.email"
            label=""
            required
          ></v-text-field>
          <v-text-field
            type="text"
            @keyup="checkNickname"
            :rules="nicknameRules"
            v-model="userData.nickname"
            label=""
            required
          ></v-text-field>
          <v-menu
            ref="menu"
            v-model="menu"
            :close-on-content-click="false"
            transition="scale-transition"
            offset-y
            min-width="290px"
          >
            <template v-slot:activator="{ on, attrs }">
              <v-text-field
                v-model="userData.birthday"
                label=""
                :rules="textRules"
                readonly
                v-bind="attrs"
                v-on="on"
                required
              ></v-text-field>
            </template>
            <v-date-picker
              ref="picker"
              v-model="userData.birthday"
              :max="new Date().toISOString().substr(0, 10)"
              min="1920-01-01"
              @change="save"
              locale="ko-kr"
            ></v-date-picker>
          </v-menu>
          <v-select
            :items="questions"
            v-model="userData.question"
            :rules="textRules"
            label="  "
            required
          ></v-select>
          <v-text-field
            v-model="userData.answer"
            label="  "
            :rules="textRules"
            type="text"
            required
          ></v-text-field>
        </v-form>
      </v-card-text>
    </v-card>

    <v-divider></v-divider>

    <v-card-actions>
      <v-btn color="primary" text @click="signUp()"></v-btn>
      <v-spacer></v-spacer>
    </v-card-actions>
  </v-card>
</template>

<script>
import axios from "axios";
/* eslint-disable no-unused-expressions */
export default {
  name: "SignUp",
  data() {
    return {
      userData: {
        username: "",
        password: "",
        nickname: "",
        email: "",
        birthday: "",
        question: "",
        answer: ""
      },
      passwdcheck: "",
      menu: false,
      isCheckingId: false,
      isCheckedId: false,
      isCheckingEmail: false,
      isCheckedEmail: false,
      isCheckingNickname: false,
      isCheckedNickname: false,
      pwRules: [
        v => !!v || " .",
        v => this.userData.password === v || "   ."
      ],
      questions: ["   ?", "   ?", "  ?"],
      textRules: [v => !!v || " ."]
    };
  },
  methods: {
    checkEmail() {
      if (!this.isCheckingEmail) {
        this.isCheckingEmail = true;
        setTimeout(() => {
          axios
            .post(`${this.$serverURL}/newuser/checkemail`, null, {
              params: {
                email: this.userData.email
              }
            })
            .then(({ data }) => {
              if (data) {
                this.isCheckedEmail = true;
              } else {
                this.isCheckedEmail = false;
              }
            })
            .catch(err => {
              console.error(err);
            });
          this.isCheckingEmail = false;
        }, 500);
      }
    },
    checkId() {
      if (!this.isCheckingId) {
        this.isCheckingId = true;
        setTimeout(() => {
          axios
            .post(`${this.$serverURL}/newuser/checkid`, null, {
              params: {
                username: this.userData.username
              }
            })
            .then(({ data }) => {
              if (data) {
                this.isCheckedId = true;
              } else {
                this.isCheckedId = false;
              }
            })
            .catch(err => {
              console.error(err);
            });
          this.isCheckingId = false;
        }, 500);
      }
    },
    checkNickname() {
      if (!this.isCheckingNickname) {
        this.isCheckingNickname = true;
        setTimeout(() => {
          axios
            .post(`${this.$serverURL}/newuser/checkNickname`, null, {
              params: {
                nickname: this.userData.nickname
              }
            })
            .then(({ data }) => {
              console.log(data);
              if (data) {
                this.isCheckedNickname = true;
              } else {
                this.isCheckedNickname = false;
              }
            })
            .catch(err => {
              console.error(err);
            });
          this.isCheckingNickname = false;
        }, 500);
      }
    },
    async signUp() {
      this.$refs.form.validate();
      if (!this.userData.username) {
        alert(" ");
      } else if (!this.isCheckedId) {
        alert("   .");
      } else if (!this.userData.password || !this.passwdcheck) {
        alert("  ");
      } else if (this.userData.password !== this.passwdcheck) {
        alert("   .");
      } else if (!this.userData.email) {
        alert(" ");
      } else if (!this.isCheckedEmail) {
        alert("   .");
      } else if (!this.userData.nickname) {
        alert(" ");
      } else if (!this.isCheckedNickname) {
        alert("   .");
      } else if (!this.userData.birthday) {
        alert(" ");
      } else if (!this.userData.question) {
        alert("   ");
      } else if (!this.userData.answer) {
        alert("    ");
      } else {
        try {
          console.log(this.userData);
          await this.$store.dispatch("SIGNUP", this.userData);
          this.$router.push({ name: "Home" });
        } catch (e) {
          alert(" .   :(");
          console.error(e);
        }
      }
    }
  },
  computed: {
    nicknameRules() {
      const rules = [];
      const rule2 = v => !!v || " ";
      rules.push(rule2);
      const rule = this.isCheckedNickname || " .";
      rules.push(rule);
      return rules;
    },
    emailRules() {
      const rules = [];
      const rule2 = v => !!v || " ";
      rules.push(rule2);
      const rule = this.isCheckedEmail || " .";
      rules.push(rule);
      return rules;
    },
    idRules() {
      const rules = [];
      const rule2 = v => !!v || " ";
      rules.push(rule2);
      const rule = this.isCheckedId || "  .";
      rules.push(rule);
      return rules;
    }
  }
};
</script>

<style></style>
