<template>
  <v-container>
    <v-row justify="center" no-gutters>
      <v-col lg="4" cols="12">
        <v-card flat height="80vh">
          <v-row justify="center" class="mt-8">
            <v-card class="mx-auto mb-4">
              <v-card-title>
                <span class="title mr-4">{{ problemTitle }}</span>
                <span class="subtitle-1  font-weight-light">{{ problemNumber }}</span>
              </v-card-title>
              <v-list-item>
                <v-icon class="mr-3">mdi-alarm</v-icon>
                <span class="body-1 mr-5"> </span>
                <span class="body-1">{{ timeLimit }}</span>
              </v-list-item>
              <v-list-item>
                <v-icon class="mr-3">mdi-memory</v-icon>
                <span class="body-1 mr-5"> </span>
                <span class="body-1">{{ memoryLimit }} MB</span>
              </v-list-item>
              <v-card-actions>
                <v-spacer></v-spacer>
                <v-btn text v-if="userSolved">  </v-btn>
              </v-card-actions>
            </v-card>
          </v-row>
          <v-row justify="center">
            <v-card flat width="20vw" height="40vh">
              <v-container class="chart-container">
                <DoughNutChart
                  v-bind:chart-data="problemData"
                  v-bind:chart-options="chartOptions"
                />
              </v-container>
            </v-card>
          </v-row>
        </v-card>
      </v-col>
      <v-divider vertical></v-divider>
      <v-col lg="5" cols="12">
        <v-card flat height="80vh">
          <v-card class="mx-5 mt-5">
            <v-list disabled>
              <v-subheader> </v-subheader>
              <v-list-item-group v-model="selectedList" color="primary">
                <v-list-item v-for="(item, i) in userSolvingData" :key="i">
                  <v-list-item-icon>
                    <v-icon v-text="item.icon" class="mr-3">mdi-alarm</v-icon>
                    <span v-if="item.avg"> </span>
                    <span v-else> </span>
                  </v-list-item-icon>
                  <v-list-item-content>
                    <v-list-item-title v-text="item.time"></v-list-item-title>
                  </v-list-item-content>
                  <v-list-item-icon>
                    <v-icon v-text="item.icon" class="mr-3">mdi-alarm</v-icon>
                    <span v-if="item.avg"> </span>
                    <span v-else> </span>
                  </v-list-item-icon>
                  <v-list-item-content>
                    <v-list-item-title v-text="item.memory" class="ml-4"></v-list-item-title>
                  </v-list-item-content>
                </v-list-item>
              </v-list-item-group>
            </v-list>
          </v-card>

          <v-card class="mx-5 mt-5">
            <v-data-table
              :lodaing="loading"
              :headers="headers"
              :items="solvedLists"
              :search="search"
            >
              <template v-slot:item.exectime="{ item }">
                <v-chip :color="getColor(item.exectime)">{{ item.exectime }}</v-chip>
              </template>
              <template v-slot:item.memory="{ item }">
                <v-chip :color="getColor(item.memory)">{{ item.memory }}</v-chip>
              </template>
            </v-data-table>
          </v-card>
        </v-card>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
import DoughNutChart from "../../components/DoughnutChart.vue";

export default {
  name: "ProblemDetail",
  components: {
    DoughNutChart
  },
  data() {
    return {
      //     
      userSolved: false,
      //   ()  
      problemTitle: "",
      problemNumber: "19541",
      timeLimit: 3,
      memoryLimit: 1024,
      //    ()  
      selectedList: 1,
      userSolvingData: [
        { avg: true, time: "20", memory: 1024 }, //  solve  
        { time: "20", memory: 2014 } //   
      ],
      // doughnutchart  
      problemData: {
        //   
        // https://vue-chartjs.org/guide/#chart-with-api-data     .
