        },
        responsive: true,
        maintainAspectRatio: false
      },
      // table  data
      timeAvg: "", //    
      memoryAvg: "", // 
      loading: true,
      search: "",
      headers: [
        {
          text: "",
          align: "start",
          sortable: false,
          filterable: false,
          value: "name"
        },
        { text: "", value: "star" },
        { text: "", value: "exectime" },
        { text: "", value: "memory" },
        { text: "", value: "lang" }
